// CustomNode.js
import React from 'react';
import { Handle, Position } from '@xyflow/react';
import { stripHtmlTagsPreserveNewlines } from './stripHtmlTagsPreserveNewlines'; // путь подстрой под себя

const CustomNode = ({ id, data, selected }) => {
  const [idLine, ...textLines] = data.label.split('\n');
  const rawMainText = textLines.join('\n');
  const cleanMainText = stripHtmlTagsPreserveNewlines(rawMainText);
  const media = data.media || [];

  const firstPhoto = media.find((m) => m.type === 'photo');

  const handleSize = selected ? 20 : 16; // Видимый размер Handle
  const sourceHandleStyle = {
    width: handleSize,
    height: handleSize,
    background: '#3b82f6',
    border: '3px solid white',
    borderRadius: '50%',
    boxShadow: '0 0 5px 2px rgba(59, 130, 246, 0.7)',
    transition: 'all 0.2s',
  };
  // Определяем размер области клика вокруг Handle
  const hotspotSize = 40; // Например, 40px на 40px
  return (
    <div
      style={{
        backgroundColor: data.bgColor || '#ffffff',
        color: 'black',
        padding: 10,
        borderRadius: 10,
        minWidth: 160,
        maxWidth: 700, // ✅ Ограничиваем ширину
        textAlign: 'center',
        boxShadow: '0 4px 12px rgba(0, 0, 0, 0.3)',
        border: selected ? '2px solid #3b82f6' : 'none',
        transition: 'border 0.2s',
        whiteSpace: 'normal', // ✅ разрешаем перенос
        wordBreak: 'break-word', // ✅ длинные слова переносятся
        overflowWrap: 'break-word', // ✅ перестраховка
      }}
    >
      {/* Заголовок */}
      <div
        style={{
          backgroundColor: 'rgba(31, 41, 55, 0.7)',
          color: 'white',
          padding: '4px 8px',
          borderRadius: 6,
          marginBottom: 8,
          fontWeight: 'bold',
        }}
      >
        {idLine}
      </div>

      {/* Основной текст */}


      <div style={{
        whiteSpace: 'pre-wrap',     // ✅ сохраняет \n как реальные переносы
        textAlign: 'left',          // ✅ выравнивание текста
        wordBreak: 'break-word',    // ✅ переносит длинные слова
        overflowWrap: 'break-word', // ✅ перестраховка
        fontSize: 14,
        marginBottom: firstPhoto ? 8 : 0
      }}>
        {cleanMainText}
      </div>


      {/* Фото превью */}
      {firstPhoto && (
        <img
          src={firstPhoto.url}
          alt="preview"
          style={{
            width: '100%',
            maxHeight: 100,
            objectFit: 'cover',
            borderRadius: 6,
          }}
        />
      )}

      <Handle type="target" position={Position.Top} style={{ visibility: 'hidden' }} />

      {/* Контейнер для увеличения области касания (hotspot) для исходящих связей */}
      <div
        style={{
          position: 'absolute',
          // Центрируем hotspot контейнер относительно нижней части узла
          bottom: -(hotspotSize / 2), // Смещаем контейнер вниз на половину его высоты, чтобы Handle был по центру
          left: '50%',
          transform: 'translateX(-50%)',
          width: hotspotSize,  // Ширина hotspot
          height: hotspotSize, // Высота hotspot
          display: 'flex',
          justifyContent: 'center',
          alignItems: 'center',
          cursor: 'pointer',
          // background: 'rgba(255, 0, 0, 0.2)', // Раскомментируйте для отладки области клика
        }}
      >
        <Handle
          type="source"
          position={Position.Bottom} // Handle позиционируется относительно этого hotspot контейнера
          style={sourceHandleStyle}
          // isConnectable={true} // isConnectable по умолчанию true, если не указано иное
        />
      </div>
    </div>
  );
};

export default CustomNode;