// ProbabilityModal.js


import React, { useState, useEffect } from 'react';

// Стили для модального окна вероятностей
const probModalOverlayStyle = { position: 'fixed', top: 0, left: 0, right: 0, bottom: 0, backgroundColor: 'rgba(0, 0, 0, 0.7)', display: 'flex', justifyContent: 'center', alignItems: 'center', zIndex: 1100 };
const probModalContentStyle = { background: 'white', padding: '25px', borderRadius: '8px', width: '90%', maxWidth: '450px', maxHeight: '70vh', overflowY: 'auto', display: 'flex', flexDirection: 'column', gap: '15px' };
const probRowStyle = { display: 'grid', gridTemplateColumns: '1fr 3fr 80px', gap: '10px', alignItems: 'center' };
const buttonStyle = { padding: '8px 12px', borderRadius: '4px', border: 'none', cursor: 'pointer' };


function ProbabilityModal({ isOpen, onClose, from, to, modifiers, onSave }) {
    const [probs, setProbs] = useState({});

    // Инициализация состояний при открытии
    useEffect(() => {
        const initialProbs = {};
        const fromNum = parseInt(from, 10);
        const toNum = parseInt(to, 10);

        if (!isNaN(fromNum) && !isNaN(toNum) && toNum >= fromNum) {
            for (let i = fromNum; i <= toNum; i++) {
                const modifier = modifiers.find(m => m.value === i);
                initialProbs[i] = modifier ? modifier.prob : 0;
            }
        }
        setProbs(initialProbs);
    }, [isOpen, from, to, modifiers]);
    
    if (!isOpen) return null;

    const handleProbChange = (value, prob) => {
        setProbs(prev => ({...prev, [value]: parseInt(prob, 10) || 0 }));
    };

    const handleSave = () => {
        // Преобразуем объект обратно в массив, сохраняя только ненулевые вероятности
        const newModifiers = Object.entries(probs)
            .filter(([_, prob]) => prob !== 0)
            .map(([value, prob]) => ({ value: parseInt(value, 10), prob }));
        onSave(newModifiers);
    };
    
    const rangeKeys = Object.keys(probs);

    return (
        <div style={probModalOverlayStyle} onClick={onClose}>
            <div style={probModalContentStyle} onClick={(e) => e.stopPropagation()}>
                <h4 style={{marginTop: 0}}>Настройка вероятностей</h4>
                {rangeKeys.length > 0 ? (
                    rangeKeys.map(value => (
                        <div key={value} style={probRowStyle}>
                            <strong>Число: {value}</strong>
                            <input 
                                type="range" 
                                min="-100" 
                                max="100" 
                                value={probs[value]} 
                                onChange={(e) => handleProbChange(value, e.target.value)}
                            />
                            <input 
                                type="number" 
                                min="-100" 
                                max="100" 
                                value={probs[value]}
                                onChange={(e) => handleProbChange(value, e.target.value)}
                                style={{width: '70px', textAlign: 'center'}}
                            />
                        </div>
                    ))
                ) : (
                    <p style={{color: '#666'}}>Задайте корректный диапазон "От" и "До", чтобы настроить вероятности.</p>
                )}
                 <div style={{ display: 'flex', justifyContent: 'flex-end', gap: '10px', marginTop: '20px' }}>
                    <button style={{ ...buttonStyle, background: '#e2e3e5' }} onClick={onClose}>
                        Отмена
                    </button>
                    <button style={{ ...buttonStyle, background: '#d4edda', color: '#155724' }} onClick={handleSave}>
                        Сохранить
                    </button>
                </div>
            </div>
        </div>
    );
}

export default ProbabilityModal;