// PaneContextMenu.js
import React from 'react';

/**
 * Контекстное меню для пустой области (панели) React Flow.
 * @param {object} props
 * @param {number} props.top - Позиция Y для меню.
 * @param {number} props.left - Позиция X для меню.
 * @param {Function} props.onClose - Функция для закрытия меню.
 * @param {Function} props.onCreateFragment - Функция для создания нового фрагмента.
 * @param {Function} props.onAddNote - Функция для добавления заметки.
 */
function PaneContextMenu({ top, left, onClose, onCreateFragment, onAddNote }) {
  const style = {
    position: 'absolute',
    top: `${top}px`,
    left: `${left}px`,
    zIndex: 1000,
  };

  const handleCreateFragment = () => {
    onCreateFragment();
    onClose();
  };

  const handleAddNote = () => {
    onAddNote();
    onClose();
  };

  return (
    <div className="context-menu" style={style}>
      <p>Действия с областью</p>
      <button onClick={handleCreateFragment}>📝 Создать фрагмент</button>
      <button onClick={handleAddNote}>🔖 Добавить заметку</button>
      <button className="delete" onClick={onClose}>❌ Закрыть</button>
    </div>
  );
}

export default PaneContextMenu;