// EdgeContextMenu.js
import React from 'react';

// Стили для меню и кнопок. Можете вынести их в отдельный CSS-файл, если хотите.
const menuStyle = {
  position: 'absolute',
  background: 'white',
  padding: '10px',
  borderRadius: '8px',
  boxShadow: '0 2px 10px rgba(0,0,0,0.2)',
  zIndex: 100,
  display: 'flex',
  flexDirection: 'column',
  gap: '8px',
  minWidth: '220px' // Немного увеличим ширину для комфорта
};

const buttonStyle = {
  background: '#f8f9fa',
  border: '1px solid #dee2e6',
  borderRadius: '4px',
  padding: '8px 12px',
  cursor: 'pointer',
  textAlign: 'left',
  width: '100%',
  display: 'flex', // Для выравнивания иконки и текста
  alignItems: 'center',
  gap: '8px', // Пространство между иконкой и текстом
};

// userId и storyId нужны для API-запроса на удаление.
// onEditEffects - это функция из App.js, которая открывает модальное окно.
// setEdges - функция для обновления состояния рёбер после удаления.
function EdgeContextMenu({ top, left, edge, onClick, onEditEffects, setEdges, userId, storyId }) {
  // Извлекаем нужные данные из объекта ребра
  const { source, target, data } = edge;

  // --- ОБРАБОТЧИКИ ДЕЙСТВИЙ ---

  // 1. Редактирование текста через простое окно prompt()
  const handleEditText = async () => {
    const newText = prompt("Введите новый текст для этого выбора:", data.text);
    
    // Если пользователь нажал "Отмена" или ничего не изменил, выходим
    if (newText === null || newText === data.text) {
      onClick(); // Закрываем меню в любом случае
      return;
    }
    
    try {
      // Обновляем данные на сервере, отправляя только новый текст
      await fetch(`/api/story/${userId}/${storyId}/choice`, {
        method: 'PUT',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify({
          source: source,
          choiceIndex: data.choiceIndex,
          text: newText,
          // effects не передаем, чтобы не затереть их
        }),
      });
      
      // Обновляем состояние локально для мгновенного отображения
      setEdges((eds) =>
        eds.map((e) => {
          if (e.id === edge.id) {
            return { ...e, data: { ...e.data, text: newText } };
          }
          return e;
        })
      );

    } catch (error) {
      alert(`Ошибка обновления текста: ${error.message}`);
    } finally {
        onClick(); // Закрываем меню после операции
    }
  };

  // 2. Удаление связи
  const handleDelete = async () => {
    if (window.confirm(`Вы уверены, что хотите удалить связь от "${source}" к "${target}"?`)) {
      try {
        // Вызываем эндпоинт для удаления
        const response = await fetch(`/api/story/${userId}/${storyId}/choice`, {
            method: 'DELETE',
            headers: { 'Content-Type': 'application/json' },
            body: JSON.stringify({
                source: source,
                choiceIndex: data.choiceIndex // Используем индекс для точной идентификации
            }),
        });

        if (!response.ok) {
            throw new Error('Ошибка ответа сервера');
        }

        // Если на сервере всё успешно, удаляем ребро из локального состояния
        setEdges((eds) => eds.filter((e) => e.id !== edge.id));
      } catch (e) {
          alert(`Ошибка удаления связи: ${e.message}`);
      }
    }
    onClick(); // Закрываем меню
  };

  return (
    // style={{ top, left }} позиционирует меню там, где был клик
    // onClick={(e) => e.stopPropagation()} предотвращает закрытие меню при клике на него самого
    <div style={{ ...menuStyle, top, left }} onClick={(e) => e.stopPropagation()}>
        {/* Заголовок меню для информации */}
        <div style={{ padding: '4px 8px', fontWeight: 'bold', borderBottom: '1px solid #eee', color: '#333' }}>
            Связь: {source} → {target}
        </div>
      
        {/* Кнопка редактирования текста */}
        <button style={buttonStyle} onClick={handleEditText}>
            ✏️ Редактировать текст
        </button>
      
        {/* Кнопка редактирования эффектов. Она просто вызывает функцию из App.js */}
        <button style={buttonStyle} onClick={onEditEffects}>
            ⚙️ Редактировать эффекты
        </button>
      
        {/* Кнопка удаления */}
        <button style={{...buttonStyle, color: '#dc3545'}} onClick={handleDelete}>
            🗑️ Удалить связь
        </button>
      
        {/* Кнопка закрытия меню */}
        <button style={buttonStyle} onClick={onClick}>
            ❌ Закрыть
        </button>
    </div>
  );
}

export default EdgeContextMenu;