// EffectsModal.js

import React, { useState, useEffect } from 'react';
import EffectRow from './EffectRow'; // Импортируем компонент строки
import { parseEffectValue, serializeEffectValue } from './effectUtils'; // Утилиты для парсинга

// Стили
const modalOverlayStyle = {
    position: 'fixed',
    top: 0,
    left: 0,
    right: 0,
    bottom: 0,
    backgroundColor: 'rgba(0, 0, 0, 0.6)',
    display: 'flex',
    justifyContent: 'center',
    alignItems: 'center',
    zIndex: 1000,
};

const modalContentStyle = {
    background: 'white',
    padding: '20px',
    borderRadius: '8px',
    width: '90%',
    maxWidth: '500px',
    maxHeight: '80vh',
    overflowY: 'auto',
    display: 'flex',
    flexDirection: 'column',
    gap: '15px',
    position: 'relative',
};

const buttonStyle = {
    padding: '8px 12px',
    borderRadius: '4px',
    border: 'none',
    cursor: 'pointer',
};

// НОВЫЙ СТИЛЬ для кнопки помощи
const helpButtonStyle = {
    ...buttonStyle,
    background: '#f0f0f0',
    color: '#333',
    border: '1px solid #ccc',
};

// НОВЫЙ ТЕКСТ для окна помощи
const helpText = `- Название атрибута может быть любым, но следите чтобы название было одним и тем же в разных переходах, любая опечатка сломает начисление/изменение значений.

- Числа только целые.

- Кнопка "скрыть" будет скрывать при прохождении истории уведомления об изменениях атрибутов. А так же кнопки которые не проходят проверку, при активации скрытия такие кнопки не будут отображаться в принципе.`;


function EffectsModal({ isOpen, onClose, onSave, edge }) {
    const [effects, setEffects] = useState([]);

    useEffect(() => {
        if (isOpen && edge?.data?.originalEffects) {
            const parsedEffects = edge.data.originalEffects.map(effect => ({
                ...effect,
                ...parseEffectValue(effect.value),
                id: Math.random(),
            }));
            setEffects(parsedEffects);
        } else {
            setEffects([]);
        }
    }, [isOpen, edge]);

    if (!isOpen) return null;

    const handleEffectChange = (index, updatedEffect) => {
        const newEffects = [...effects];
        newEffects[index] = updatedEffect;
        setEffects(newEffects);
    };

    const handleAddEffect = () => {
        setEffects([
            ...effects,
            {
                id: Math.random(),
                stat: '',
                hide: false,
                type: 'set',
                value: '',
                modifiers: [],
            },
        ]);
    };

    const handleDeleteEffect = (index) => {
        setEffects(effects.filter((_, i) => i !== index));
    };

    const handleSave = () => {
        const serializedEffects = effects.map(effect => {
            if (!effect.stat.trim()) return null;
            const { value, modifiers } = serializeEffectValue(effect);
            return {
                stat: effect.stat,
                hide: effect.hide,
                value,
                modifiers,
            };
        }).filter(Boolean);

        if (serializedEffects.length !== effects.filter(e => e.stat.trim()).length) {
            alert('Пожалуйста, заполните все необходимые поля.');
            return;
        }

        onSave(serializedEffects);
    };

    return (
        <div style={modalOverlayStyle} onClick={onClose}>
            <div style={modalContentStyle} onClick={(e) => e.stopPropagation()}>
                <h3 style={{ marginTop: 0, borderBottom: '1px solid #eee', paddingBottom: '10px' }}>
                    Редактирование эффектов: {edge.source} → {edge.target}
                </h3>

                <div style={{ display: 'flex', flexDirection: 'column', gap: '15px' }}>
                    {effects.map((effect, index) => (
                        <EffectRow
                            key={effect.id}
                            effect={effect}
                            onChange={(updatedEffect) => handleEffectChange(index, updatedEffect)}
                            onDelete={() => handleDeleteEffect(index)}
                        />
                    ))}
                </div>

                <button
                    style={{ ...buttonStyle, background: '#cce5ff', color: '#004085', marginTop: '15px' }}
                    onClick={handleAddEffect}
                >
                    + Добавить эффект
                </button>

                {/* --- ИЗМЕНЕННЫЙ БЛОК КНОПОК --- */}
                <div style={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center', gap: '10px', marginTop: '20px' }}>
                    {/* Кнопка помощи слева */}
                    <button 
                        style={helpButtonStyle} 
                        onClick={() => alert(helpText)}
                        title="Показать справку"
                    >
                        ? Помощь
                    </button>

                    {/* Кнопки действий справа */}
                    <div style={{ display: 'flex', gap: '10px' }}>
                        <button style={{ ...buttonStyle, background: '#e2e3e5' }} onClick={onClose}>
                            Отмена
                        </button>
                        <button style={{ ...buttonStyle, background: '#d4edda', color: '#155724' }} onClick={handleSave}>
                            Сохранить изменения
                        </button>
                    </div>
                </div>
            </div>
        </div>
    );
}

export default EffectsModal;