// api.js

export async function apiUpdateFragmentText(userId, storyId, fragmentId, newText) {
 console.log('[apiUpdateFragmentText] Posting text:', JSON.stringify(newText));
 await fetch(`/api/story/${userId}/${storyId}/fragment/${fragmentId}/text`, {
  method: 'POST',
  headers: { 'Content-Type': 'application/json' },
  body: JSON.stringify({ text: newText }),
 });
}

export async function apiAddMediaToFragment(userId, storyId, fragmentId, mediaObj) {
 const response = await fetch(`/api/story/${userId}/${storyId}/fragment/${fragmentId}/add_media`, {
  method: 'POST',
  headers: { 'Content-Type': 'application/json' },
  body: JSON.stringify(mediaObj),
 });
 if (!response.ok) {
   const error = await response.json();
   throw new Error(error.error || 'Failed to add media');
 }
 return await response.json();
}

export async function apiUpdateFragmentMedia(userId, storyId, fragmentId, mediaArray) {
  const response = await fetch(`/api/story/${userId}/${storyId}/fragment/${fragmentId}/media`, {
    method: 'PUT',
    headers: { 'Content-Type': 'application/json' },
    body: JSON.stringify({ media: mediaArray }),
  });
  if (!response.ok) {
   const error = await response.json();
   throw new Error(error.error || 'Failed to update media');
 }
 return await response.json();
}

