// --- НОВАЯ ФУНКЦИЯ: Создание несвязанного фрагмента ---
export async function apiCreateStandaloneFragment(userId, storyId, newName) {
    const response = await fetch(`/api/story/${userId}/${storyId}/create_fragment`, {
        method: 'POST',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify({ newName }),
    });
    if (!response.ok) {
        const { error } = await response.json();
        throw new Error(error);
    }
    return await response.json();
}

// --- ОБНОВЛЕННАЯ ФУНКЦИЯ: Создание заметки ---
export async function apiCreateNote(userId, storyId, text, position) {
    const response = await fetch(`/api/story/${userId}/${storyId}/bookmarks`, {
        method: 'POST',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify({ text, position }),
    });
    if (!response.ok) {
        const { error } = await response.json();
        throw new Error(error || 'Server error');
    }
    // Теперь сервер возвращает созданную заметку
    return await response.json(); 
}

// --- НОВАЯ ФУНКЦИЯ: Загрузка всех заметок ---
export async function apiLoadBookmarks(userId, storyId) {
    const response = await fetch(`/api/story/${userId}/${storyId}/bookmarks`);
    if (!response.ok) {
        // Не критичная ошибка, просто выводим в консоль
        console.error("Could not load bookmarks");
        return {};
    }
    return await response.json();
}


export async function apiUpdateChoice(userId, storyId, sourceId, choiceIndex, updates) {
 const response = await fetch(`/api/story/${userId}/${storyId}/choice`, {
  method: 'PUT',
  headers: { 'Content-Type': 'application/json' },
  body: JSON.stringify({
   source: sourceId,
   choiceIndex: choiceIndex,
   ...updates
  }),
 });
 if (!response.ok) {
  const { error } = await response.json();
  throw new Error(error);
 }
 return await response.json();
}

