// --- НОВАЯ ФУНКЦИЯ: Обновление текста заметки ---
export async function apiUpdateNote(userId, storyId, noteId, text) {
    const response = await fetch(`/api/story/${userId}/${storyId}/bookmarks/${noteId}`, {
        method: 'PUT', // Используем метод PUT для обновления
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify({ text }), // Отправляем только новое содержимое текста
    });
    if (!response.ok) {
        const { error } = await response.json();
        throw new Error(error || 'Server error on update');
    }
    return await response.json(); // Ожидаем ответ {"status": "ok"}
}

// --- НОВАЯ ФУНКЦИЯ: Удаление заметки ---
export async function apiDeleteNote(userId, storyId, noteId) {
    const response = await fetch(`/api/story/${userId}/${storyId}/bookmarks/${noteId}`, {
        method: 'DELETE', // Используем метод DELETE для удаления
    });
    if (!response.ok) {
        const { error } = await response.json();
        throw new Error(error || 'Server error on delete');
    }
    return await response.json(); // Ожидаем ответ {"status": "ok"}
}