//MultiNodeContextMenu.js

import React from 'react';

const MultiNodeContextMenu = ({ top, left, onClose, nodes, onDeleteAll, onCalculate }) => {
  const style = {
    position: 'absolute',
    top,
    left,
    zIndex: 1000,
    backgroundColor: 'white',
    border: '1px solid #ccc',
    borderRadius: '8px',
    boxShadow: '0 2px 8px rgba(0,0,0,0.15)',
    padding: '8px',
    display: 'flex',
    flexDirection: 'column',
    gap: '5px',
  };

  const buttonStyle = {
    background: 'none',
    border: 'none',
    padding: '8px 12px',
    textAlign: 'left',
    cursor: 'pointer',
    borderRadius: '4px',
    width: '100%',
  };
  
  const buttonHoverStyle = (e) => {
    e.target.style.backgroundColor = '#f0f0f0';
  };
  
  const buttonLeaveStyle = (e) => {
    e.target.style.backgroundColor = 'transparent';
  };

  const handleDelete = () => {
    // Передаем список выделенных узлов в функцию удаления
    onDeleteAll(nodes); 
  };
  
  const handleCalculate = () => {
    // Заглушка для функции "Просчитать"
    onCalculate(nodes);
  };

  return (
    <div style={style} onClick={(e) => e.stopPropagation()}>
      <button 
        style={buttonStyle}
        onMouseEnter={buttonHoverStyle}
        onMouseLeave={buttonLeaveStyle}
        onClick={handleDelete}
      >
        🗑 Удалить всё ({nodes.length})
      </button>
      <button 
        style={buttonStyle}
        onMouseEnter={buttonHoverStyle}
        onMouseLeave={buttonLeaveStyle}
        onClick={handleCalculate}
      >
        🎛 Просчитать
      </button>
      <div style={{ borderTop: '1px solid #eee', margin: '4px 0' }}></div>
      <button 
        style={buttonStyle}
        onMouseEnter={buttonHoverStyle}
        onMouseLeave={buttonLeaveStyle}
        onClick={onClose}
      >
        ❌ Закрыть меню
      </button>
    </div>
  );
};

export default MultiNodeContextMenu;