// App.js старый раб

import React, { useCallback, useEffect, useState, useRef, useMemo } from 'react';
import {
  ReactFlow,
  Background,
  Controls,
  MiniMap,
  useNodesState,
  useEdgesState,
  useReactFlow,
  ReactFlowProvider,
} from '@xyflow/react';
import '@xyflow/react/dist/style.css';
import dagre from 'dagre';

import CustomEdge from './CustomEdge';
import CustomNode from './CustomNode';
import NoteNode from './NoteNode';
import NoteContextMenu from './NoteContextMenu';
import ContextMenu from './ContextMenu';
import { CreateFragmentModal } from './CreateFragmentModal';
import EdgeContextMenu from './EdgeContextMenu';
import PaneContextMenu from './PaneContextMenu';
import EffectsModal from './EffectsModal';
import { TextEditorModal } from './ContextMenu';
import MultiNodeContextMenu from './MultiNodeContextMenu';
import { parseEffectValue, applyEffects, calculateAllEffects } from './effectsUtils';
import {
  apiUpdateFragmentText,
  apiDeleteFragments,
  apiRenameFragment,
  apiConnectFragments,
  apiCreateAndConnect,
  apiUpdateChoice,
  apiCreateStandaloneFragment,
  apiLoadBookmarks,
  apiUpdateNote,
  apiDeleteNote,
  apiCreateNote,
} from './api';


const dagreGraph = new dagre.graphlib.Graph();
dagreGraph.setDefaultEdgeLabel(() => ({}));
const nodeWidth = 700;
const nodeHeight = 36;
const getLayoutedElements = (nodes, edges, direction = 'TB') => {
  dagreGraph.setGraph({ rankdir: direction, nodesep: 100, ranksep: 150 });
  nodes.forEach((node) => dagreGraph.setNode(node.id, { width: nodeWidth, height: nodeHeight }));
  edges.forEach((edge) => dagreGraph.setEdge(edge.source, edge.target));
  dagre.layout(dagreGraph);
  const mainNodeId = 'main_1';
  const mainNodePos = dagreGraph.node(mainNodeId);
  const offsetX = mainNodePos?.x || 0;
  const offsetY = mainNodePos?.y || 0;
  nodes.forEach((node) => {
    const nodeWithPos = dagreGraph.node(node.id);
    node.targetPosition = 'top';
    node.sourcePosition = 'bottom';
    node.position = { x: nodeWithPos.x - offsetX, y: nodeWithPos.y - offsetY };
  });
  return { nodes, edges };
};


const getNodeColor = (id, fragment) => {
    const hasText = !!fragment.text?.trim() && fragment.text !== '(пусто)';
    const hasMedia = Array.isArray(fragment.media) && fragment.media.length > 0;
    if (!hasText && !hasMedia) return '#fecaca';
    if (!fragment.choices || fragment.choices.length === 0) return '#fef3c7';
    if (id === 'main_1') return '#86efac';
    if (/_1$/.test(id) && id !== 'main_1') return '#bbf7d0';
    return '#e0f2fe';
};

const mediaLabel = (type) => ({
    photo: 'фото',
    video: 'видео',
    animation: 'анимации',
    audio: 'аудио'
}[type] || type);


function App() {
  const [nodes, setNodes, onNodesChange] = useNodesState([]);
  const [edges, setEdges, onEdgesChange] = useEdgesState([]);
  const [menu, setMenu] = useState(null);
  const [edgeMenu, setEdgeMenu] = useState(null);
  const [paneMenu, setPaneMenu] = useState(null);
  const [effectsModal, setEffectsModal] = useState({ isOpen: false, edge: null });
  const [showMiniMap, setShowMiniMap] = useState(true);
  const wasConnectedRef = useRef(false);
  const [noteMenu, setNoteMenu] = useState(null);
  const reactFlowWrapper = useRef(null);
  const connectingNodeId = useRef(null);
  const { screenToFlowPosition, getNodes } = useReactFlow();
  const [multiNodeMenu, setMultiNodeMenu] = useState(null);

  const { userId, storyId } = useMemo(() => {
    const pathname = window.location.pathname;
    const [uid, sid] = pathname.slice(1).split('_');
    return { userId: uid, storyId: sid };
  }, []);
  // ✅ Новое состояние для управления модальным окном
  const [fragmentModal, setFragmentModal] = useState({
    isOpen: false,
    sourceNodeId: null,
    position: null,
    defaultName: '', // <-- ДОБАВЛЕНО
  });
const generateUniqueFragmentId = (inputName, existingIds, isStrict = false) => {
    // Регулярное выражение для извлечения базового имени и номера
    const regex = /^(.*?)(?:_(\d+))?$/;
    const match = inputName.match(regex);

    // Если имя не соответствует формату, это ошибка
    if (!match) {
        return { error: `Некорректный формат имени: "${inputName}"` };
    }

    const base = match[1];
    // Получаем текущий номер или 0, если его нет
    const currentNumber = match[2] ? parseInt(match[2], 10) : 0;
    const normalizedExistingIds = new Set([...existingIds].map(id => id.toLowerCase()));

    // РЕЖИМ СТРОГОЙ ПРОВЕРКИ: используется при отправке формы
    // Проверяет, существует ли уже введенное пользователем имя
    if (isStrict) {
        if (normalizedExistingIds.has(inputName.toLowerCase())) {
            return { error: `Фрагмент с именем "${inputName}" уже существует.` };
        }
        // Также проверяем, что имя не пустое и не состоит только из пробелов
        if (!inputName.trim()) {
            return { error: "Имя фрагмента не может быть пустым." };
        }
        return { id: inputName };
    }

    // РЕЖИM ГЕНЕРАЦИИ: используется для placeholder
    // Начинаем поиск со следующего номера
    let nextNumber = currentNumber + 1;
    while (normalizedExistingIds.has(`${base.toLowerCase()}_${nextNumber}`)) {
        nextNumber++;
    }
    return { id: `${base}_${nextNumber}` };
};

  const saveCurrentNodePositions = useCallback(() => {
    if (!userId || !storyId) return;

    const currentNodes = getNodes();
    if (!currentNodes.length) return;

    const positionsToSave = currentNodes.reduce((acc, node) => {
      acc[node.id] = node.position;
      return acc;
    }, {});

    fetch(`/api/story/${userId}/${storyId}/positions`, {
      method: 'POST',
      headers: { 'Content-Type': 'application/json' },
      body: JSON.stringify(positionsToSave),
    })
    .then(res => {
      if(!res.ok) console.error("Ошибка при сохранении позиций");
    })
    .catch(error => console.error("Сетевая ошибка при сохранении позиций:", error));
  }, [userId, storyId, getNodes]);

  const processStoryData = useCallback((storyData, bookmarksData, positionsData) => {
    if (!storyData?.fragments) return;

    let initialNodes = [];
    const initialEdges = [];

    Object.entries(storyData.fragments).forEach(([fragmentId, fragment]) => {
      const mediaCounts = (fragment.media || []).reduce((acc, { type }) => ({ ...acc, [type]: (acc[type] || 0) + 1 }), {});
      const mediaDescription = Object.entries(mediaCounts).map(([type, count]) => `${count} ${mediaLabel(type)}`).join(', ');
      const labelText = [fragmentId, fragment.text || '(пусто)', mediaDescription && `📎 ${mediaDescription}`].filter(Boolean).join('\n');
      const extendedMedia = (fragment.media || []).map(m => ({ ...m, url: `/api/tgfile/${m.file_id}` }));

      initialNodes.push({
        id: fragmentId,
        type: 'custom',
        position: { x: 0, y: 0 },
        data: {
          label: labelText,
          bgColor: getNodeColor(fragmentId, fragment),
          media: extendedMedia,
          fragment: fragment,
        },
      });

      if (fragment.choices) {
        fragment.choices.forEach((choice, i) => {
          const effectText = choice.effects?.length ? choice.effects.map(e => `${e.stat}: ${e.value}`).join(', ') : '';
          initialEdges.push({
            id: `${fragmentId}->${choice.target}_${i}`,
            source: fragmentId,
            target: choice.target,
            type: 'custom',
            animated: true,
            style: { stroke: '#2563eb' },
            data: {
              text: choice.text,
              effectsText: effectText,
              originalEffects: choice.effects || [],
              choiceIndex: i,
            },
          });
        });
      }
    });

    if (bookmarksData) {
        Object.entries(bookmarksData).forEach(([bookmarkId, bookmark]) => {
            initialNodes.push({
                id: bookmarkId,
                type: 'note',
                position: bookmark.position,
                data: { text: bookmark.text },
                draggable: true,
                selectable: true,
            });
        });
    }
    const hasPositions = positionsData && Object.keys(positionsData).length > 0;

    if (hasPositions) {
        initialNodes.forEach(node => {
            if (positionsData[node.id]) {
                node.position = positionsData[node.id];
            }
        });
        setNodes(initialNodes);
        setEdges(initialEdges);
    } else {
        const fragmentNodes = initialNodes.filter(n => n.type !== 'note');
        const noteNodes = initialNodes.filter(n => n.type === 'note');
        const { nodes: layoutedNodes, edges: layoutedEdges } = getLayoutedElements(fragmentNodes, initialEdges);
        setNodes([...layoutedNodes, ...noteNodes]);
        setEdges(layoutedEdges);
    }
  }, [setNodes, setEdges]);


  useEffect(() => {
    const fetchData = async () => {
        if (!userId || !storyId) return;
        try {
            const [storyRes, positionsRes, bookmarksRes] = await Promise.all([
                fetch(`/api/story/${userId}/${storyId}`),
                fetch(`/api/story/${userId}/${storyId}/positions`),
                apiLoadBookmarks(userId, storyId)
            ]);

            const storyData = await storyRes.json();
            if (storyData.error) throw new Error(storyData.error);
            
            let positionsData = {};
            if (positionsRes.ok) {
                positionsData = await positionsRes.json();
            }
            
            processStoryData(storyData, bookmarksRes, positionsData);

        } catch (error) {
            console.error("Ошибка при загрузке данных:", error);
            alert("Не удалось загрузить историю или ее карту.");
        }
    };
    fetchData();
  }, [userId, storyId, processStoryData]);

  useEffect(() => {
    const container = reactFlowWrapper.current?.querySelector('.react-flow__renderer');

    const handleContextMenu = (event) => {
      if (event.target.classList.contains('react-flow__pane')) return;

      const selectedNodes = getNodes().filter(n => n.selected);
      if (selectedNodes.length > 1) {
        event.preventDefault();
        setMenu(null);
        setNoteMenu(null);
        setEdgeMenu(null);
        setPaneMenu(null);

        setMultiNodeMenu({
          top: event.clientY,
          left: event.clientX,
          nodes: selectedNodes,
        });
      }
    };

    container?.addEventListener('contextmenu', handleContextMenu);
    return () => container?.removeEventListener('contextmenu', handleContextMenu);
  }, [getNodes]);

  const onConnectStart = useCallback((event, { nodeId }) => {
    connectingNodeId.current = nodeId;
    wasConnectedRef.current = false;
  }, []);

  const onConnect = useCallback(async (connection) => {
    if (!connection.source || !connection.target) return;
    
    wasConnectedRef.current = true;

    const choiceText = prompt('Введите текст для кнопки этого выбора:');
    if (!choiceText) return;
    
    try {
      await apiConnectFragments(userId, storyId, connection.source, connection.target, choiceText);

      let newChoiceIndex;

      setNodes((nds) =>
        nds.map((node) => {
          if (node.id === connection.source) {
            const newChoice = { target: connection.target, text: choiceText, effects: [] };
            newChoiceIndex = node.data.fragment.choices ? node.data.fragment.choices.length : 0;

            const updatedChoices = node.data.fragment.choices
              ? [...node.data.fragment.choices, newChoice]
              : [newChoice];

            return {
              ...node,
              data: {
                ...node.data,
                fragment: {
                  ...node.data.fragment,
                  choices: updatedChoices,
                },
              },
            };
          }
          return node;
        })
      );

      const newEdge = {
        id: `${connection.source}->${connection.target}_${newChoiceIndex}`,
        source: connection.source,
        target: connection.target,
        type: 'custom',
        animated: true,
        style: { stroke: '#2563eb' },
        data: {
          text: choiceText,
          effectsText: '',
          originalEffects: [],
          choiceIndex: newChoiceIndex,
        },
      };

      setEdges((eds) => eds.concat(newEdge));
    } catch (e) {
      alert(`Ошибка создания связи: ${e.message}`);
    }
  }, [userId, storyId, setEdges, setNodes]);
  // ✅ ОБНОВЛЕННАЯ функция onConnectEnd
  const onConnectEnd = useCallback((event) => {
    if (wasConnectedRef.current) {
      wasConnectedRef.current = false;
      return;
    }

    const sourceNodeId = connectingNodeId.current;
    connectingNodeId.current = null;

    if (!sourceNodeId) return;

    let clientX = event.clientX;
    let clientY = event.clientY;

    if (event.type === 'touchend' && event.changedTouches?.length > 0) {
      clientX = event.changedTouches[0].clientX;
      clientY = event.changedTouches[0].clientY;
    }

    if (typeof clientX !== 'number' || typeof clientY !== 'number') return;

    // ✅ ИЗМЕНЕНИЕ: Генерируем имя здесь и сохраняем в стейт
    const currentNodeIds = new Set(getNodes().map(n => n.id));
    const { id: suggestedName } = generateUniqueFragmentId(sourceNodeId, currentNodeIds);

    setFragmentModal({
      isOpen: true,
      sourceNodeId,
      position: screenToFlowPosition({ x: clientX, y: clientY }),
      defaultName: suggestedName || '', // <-- СОХРАНЯЕМ
    });

  }, [screenToFlowPosition, getNodes, generateUniqueFragmentId]);


  const onNodeDragStop = useCallback(() => {
      saveCurrentNodePositions();
  }, [saveCurrentNodePositions]);

  const handleDeleteSelectedNodes = useCallback(async (nodesToDelete) => {
      const fragmentNodesToDelete = nodesToDelete.filter(n => n.type !== 'note');
      if (fragmentNodesToDelete.length === 0) {
          setMultiNodeMenu(null);
          return;
      }

      const nodeIdsToDelete = fragmentNodesToDelete.map(n => n.id);

      if (!window.confirm(`Вы уверены, что хотите удалить ${nodeIdsToDelete.length} фрагмент(а)? Это действие необратимо.`)) {
          return;
      }
      
      try {
          await apiDeleteFragments(userId, storyId, nodeIdsToDelete);

          setNodes(currentNodes => currentNodes.filter(n => !nodeIdsToDelete.includes(n.id)));
          setEdges(currentEdges => 
              currentEdges.filter(e => !nodeIdsToDelete.includes(e.source) && !nodeIdsToDelete.includes(e.target))
          );
          
          console.log(`Успешно удалено ${nodeIdsToDelete.length} фрагментов.`);

      } catch (error) {
          alert("Произошла ошибка при удалении одного или нескольких фрагментов.");
          console.error("Ошибка массового удаления:", error);
      } finally {
          setMultiNodeMenu(null);
      }
  }, [userId, storyId, setNodes, setEdges]);

  const handleCalculate = useCallback((nodesToCalculate) => {
    const result = calculateAllEffects(nodesToCalculate);
    alert(result);
    setMultiNodeMenu(null);
  }, []);

  const handleNodeInteraction = useCallback((event, node) => {
    event.preventDefault();
    event.stopPropagation();

    const allNodes = getNodes();
    const selectedNodes = allNodes.filter(n => n.selected);
    
    if (selectedNodes.length > 1) {
        if (selectedNodes.find(n => n.id === node.id)) {
            setMenu(null);
            setNoteMenu(null);
            setMultiNodeMenu({
                top: event.clientY,
                left: event.clientX,
                nodes: selectedNodes,
            });
        }
    } else {
        setMultiNodeMenu(null);
        if (node.type === 'note') {
            setNoteMenu({
                id: node.id,
                node: node,
                top: event.clientY,
                left: event.clientX,
            });
        } else {
            setMenu({
                id: node.id,
                top: event.clientY,
                left: event.clientX,
                node: node,
            });
        }
    }
  }, [getNodes, setMenu, setNoteMenu, setMultiNodeMenu]);

  const handleEdgeInteraction = useCallback((event, edge) => {
    event.preventDefault();
    event.stopPropagation();
    setEdgeMenu({
      id: edge.id,
      top: event.clientY,
      left: event.clientX,
      edge: edge,
    });
  }, [setEdgeMenu]);

  const closeAllMenus = useCallback(() => {
    setMenu(null);
    setEdgeMenu(null);
    setPaneMenu(null);
    setNoteMenu(null);
    setMultiNodeMenu(null);
  }, []);

// ✅ ДОБАВЬТЕ ЭТОТ ОБРАБОТЧИК
const onMoveStart = useCallback(() => {
  closeAllMenus();
}, [closeAllMenus]);

  const onPaneClick = useCallback(() => {
    closeAllMenus();
  }, [closeAllMenus]);


  const onPaneContextMenu = useCallback((event) => {
    event.preventDefault();
    closeAllMenus(); // Закрываем другие меню на всякий случай
    
    setPaneMenu({
      top: event.clientY,
      left: event.clientX,
      flowPosition: screenToFlowPosition({ x: event.clientX, y: event.clientY }),
    });
  }, [screenToFlowPosition, closeAllMenus]);

  const handleCreateFragment = useCallback(async (position) => {
      let rawInput = prompt("Введите имя нового фрагмента (например, Сцена_1):");
      if (!rawInput) return;

      // Клиентская проверка на уникальность и пустое имя остается как есть
      const currentNodeIds = new Set(getNodes().map((n) => n.id));
      // Используем строгую проверку, так как это финальное имя перед отправкой
      const { id: newName, error: clientError } = generateUniqueFragmentId(
        rawInput.trim(),
        currentNodeIds,
        true // Включаем строгий режим
      );

      if (clientError) {
        alert(clientError);
        return;
      }

      try {
          // Вызываем API, как и раньше
          const { story } = await apiCreateStandaloneFragment(userId, storyId, newName);

          // Проверяем, вернул ли сервер данные, на случай непредвиденных ошибок
          const newFragmentData = story.fragments[newName];
          if (!newFragmentData) {
              throw new Error("Сервер не вернул данные для нового фрагмента.");
          }

          // Создаем новую ноду при успехе
          const newNode = {
              id: newName,
              type: 'custom',
              position,
              data: {
                  label: `${newName}\n(пусто)`,
                  bgColor: getNodeColor(newName, newFragmentData),
                  media: [],
                  fragment: newFragmentData,
              },
          };

          setNodes((nds) => nds.concat(newNode));
          setTimeout(saveCurrentNodePositions, 100);

      } catch (e) {
          // --- КЛЮЧЕВЫЕ ИЗМЕНЕНИЯ ЗДЕСЬ ---
          // Проверяем, содержит ли ошибка в себе ответ от сервера (response)
          if (e.response) {
              // Пытаемся асинхронно получить JSON из ответа
              e.response.json().then(jsonError => {
                  // Если в JSON есть ключ "error", показываем его пользователю
                  alert(`Ошибка с сервера: ${jsonError.error || 'Неизвестная ошибка'}`);
              }).catch(() => {
                  // Если ответ не в формате JSON, показываем общий текст ошибки
                  alert(`Не удалось обработать ошибку сервера: ${e.message}`);
              });
          } else {
              // Для других типов ошибок (например, сетевых) оставляем старое поведение
              alert(`Ошибка создания фрагмента: ${e.message}`);
          }
          console.error(e); // Оставляем вывод в консоль для отладки
      }
  }, [userId, storyId, getNodes, setNodes, saveCurrentNodePositions, generateUniqueFragmentId]);

  // ✅ НОВАЯ функция для обработки создания нового связанного фрагмента
const handleCreateConnectedFragment = useCallback(async (newName, choiceText) => {
    const { sourceNodeId, position } = fragmentModal;
    if (!sourceNodeId || !position || !newName || !choiceText) {
        // Эта ошибка не должна происходить, но для надежности
        throw new Error("Отсутствуют необходимые данные для создания фрагмента.");
    }

    try {
        const { story } = await apiCreateAndConnect(userId, storyId, sourceNodeId, newName, choiceText);
        const sourceFragment = story.fragments[sourceNodeId];
        const newChoiceIndex = sourceFragment.choices.findIndex(c => c.target === newName);

        if (newChoiceIndex === -1) {
            throw new Error("Не удалось найти созданный выбор в ответе от сервера.");
        }

        const newNode = {
            id: newName,
            type: 'custom',
            position,
            data: {
                label: `${newName}\n(пусто)`,
                bgColor: '#fecaca',
                media: [],
                fragment: story.fragments[newName],
            },
        };

        const newEdge = {
            id: `${sourceNodeId}->${newName}_${newChoiceIndex}`,
            source: sourceNodeId,
            target: newName,
            type: 'custom',
            animated: true,
            style: { stroke: '#2563eb' },
            data: {
                text: choiceText,
                effectsText: '',
                originalEffects: [],
                choiceIndex: newChoiceIndex,
            },
        };

        setNodes((nds) => {
            const updatedNodes = nds.map((node) => {
                if (node.id === sourceNodeId) {
                    const updatedFragment = story.fragments[sourceNodeId];
                    const mediaCounts = (updatedFragment.media || []).reduce((acc, { type }) => ({ ...acc, [type]: (acc[type] || 0) + 1 }), {});
                    const mediaDescription = Object.entries(mediaCounts).map(([type, count]) => `${count} ${mediaLabel(type)}`).join(', ');
                    const newLabel = [sourceNodeId, updatedFragment.text || '(пусто)', mediaDescription && `📎 ${mediaDescription}`].filter(Boolean).join('\n');
                    return {
                        ...node,
                        data: {
                            ...node.data,
                            fragment: updatedFragment,
                            label: newLabel,
                            bgColor: getNodeColor(sourceNodeId, updatedFragment),
                        },
                    };
                }
                return node;
            });
            return [...updatedNodes, newNode];
        });

        setEdges((eds) => eds.concat(newEdge));

        // При успехе - закрываем модальное окно
        setFragmentModal({ isOpen: false, sourceNodeId: null, position: null, defaultName: '' });
        
        // Сохраняем позиции асинхронно
        setTimeout(saveCurrentNodePositions, 100);

    } catch (e) {
        // Пробрасываем ошибку дальше, чтобы ее поймало модальное окно
        console.error("Ошибка API при создании фрагмента:", e);
        throw e; // Это ключевое изменение!
    }
}, [userId, storyId, setNodes, setEdges, saveCurrentNodePositions, fragmentModal]);



  const handleAddNote = useCallback(async (position) => {
    try {
      const { status, bookmark } = await apiCreateNote(userId, storyId, "(пусто)", position);
      
      if (status === 'ok' && bookmark) {
        const newNoteNode = {
          id: bookmark.id,
          type: 'note',
          position: bookmark.position,
          data: { text: bookmark.text },
          draggable: true,
          selectable: true,
        };
        
        setNodes((nds) => nds.concat(newNoteNode));
        
        setNoteMenu({
          node: newNoteNode,
          top: window.innerHeight / 2,
          left: window.innerWidth / 2,
          autoOpenEditor: true,
        });

        setTimeout(saveCurrentNodePositions, 100);
      } else {
        throw new Error('Сервер не вернул данные о заметке.');
      }
    } catch(e) {
      alert(`Ошибка создания заметки: ${e.message}`);
    }
  }, [userId, storyId, setNodes, saveCurrentNodePositions]);

  const handleSaveEffects = useCallback(async (newEffects) => {
    const { edge } = effectsModal;
    if (!edge) return;

    try {
      await apiUpdateChoice(userId, storyId, edge.source, edge.data.choiceIndex, {
        effects: newEffects
      });

      setEdges((eds) =>
        eds.map((e) => {
          if (e.id === edge.id) {
            const newEffectText = newEffects.length ? newEffects.map(ef => `${ef.stat}: ${ef.value}`).join(', ') : '';
            return {
              ...e,
              data: {
                ...e.data,
                originalEffects: newEffects,
                effectsText: newEffectText,
              },
            };
          }
          return e;
        })
      );

      setEffectsModal({ isOpen: false, edge: null });
    } catch (e) {
      alert(`Ошибка сохранения эффектов: ${e.message}`);
    }
  }, [effectsModal, userId, storyId, setEdges]);


  const nodeTypes = useMemo(() => ({
    custom: CustomNode,
    note: NoteNode,
  }), []);
  const edgeTypes = useMemo(() => ({ custom: CustomEdge }), []);


  return (
    <div style={{ height: '100vh', width: '100%' }} ref={reactFlowWrapper}>
      <ReactFlow
        nodes={nodes}
        edges={edges}
        onNodesChange={onNodesChange}
        onEdgesChange={onEdgesChange}
        nodeTypes={nodeTypes}
        edgeTypes={edgeTypes}
        onConnect={onConnect}
        onConnectStart={onConnectStart}
        onConnectEnd={onConnectEnd}
        onPaneClick={onPaneClick}
        onPaneContextMenu={onPaneContextMenu}
        onNodeDragStop={onNodeDragStop}
        onNodeClick={handleNodeInteraction}
        onNodeContextMenu={handleNodeInteraction}
        onEdgeClick={handleEdgeInteraction}
        onEdgeContextMenu={handleEdgeInteraction}
        onMoveStart={onMoveStart}             // ✅ ДОБАВЬТЕ ЭТУ СТРОКУ
        fitView
        minZoom={0.1}
        maxZoom={2}
      >
        <Controls />

        <div
          style={{
            position: 'absolute',
            bottom: 10,
            left: 60,
            zIndex: 100,
          }}
        >
          <button
            onClick={() => setShowMiniMap(!showMiniMap)}
            style={{
              background: '#fff',
              border: '1px solid #ccc',
              borderRadius: '6px',
              padding: '4px 8px',
              cursor: 'pointer',
              fontSize: '12px'
            }}
          >
            {showMiniMap ? 'Миникарта' : 'Миникарта'}
          </button>
        </div>


        <Background />
        
        {showMiniMap && (
          <MiniMap nodeColor={(node) => node.data.bgColor} pannable zoomable />
        )}
        
        {menu && (
          <ContextMenu
            onClick={onPaneClick}
            setNodes={setNodes}
            setEdges={setEdges}
            {...menu}
          />
        )}
        
        {edgeMenu && (
          <EdgeContextMenu
            {...edgeMenu}
            userId={userId}
            storyId={storyId}
            onClick={() => setEdgeMenu(null)}
            onEditEffects={() => {
              setEffectsModal({ isOpen: true, edge: edgeMenu.edge });
              setEdgeMenu(null);
            }}
            setEdges={setEdges}
          />
        )}
        {paneMenu && (
            <PaneContextMenu
                {...paneMenu}
                onClose={() => setPaneMenu(null)}
                onCreateFragment={() => handleCreateFragment(paneMenu.flowPosition)}
                onAddNote={() => handleAddNote(paneMenu.flowPosition)}
            />
        )}    
      {noteMenu && (
        <NoteContextMenu
          node={noteMenu.node}
          setNodes={setNodes}
          onClose={() => setNoteMenu(null)}
          top={noteMenu.top}
          left={noteMenu.left}
          autoOpenEditor={noteMenu.autoOpenEditor}
        />
      )}        
       {multiNodeMenu && (
            <MultiNodeContextMenu
                top={multiNodeMenu.top}
                left={multiNodeMenu.left}
                nodes={multiNodeMenu.nodes}
                onClose={() => setMultiNodeMenu(null)}
                onDeleteAll={handleDeleteSelectedNodes}
                onCalculate={handleCalculate}
            />
        )}
      </ReactFlow>

      <EffectsModal
        isOpen={effectsModal.isOpen}
        edge={effectsModal.edge}
        onClose={() => setEffectsModal({ isOpen: false, edge: null })}
        onSave={handleSaveEffects}
      />
      <CreateFragmentModal
        isOpen={fragmentModal.isOpen}
        onClose={() => setFragmentModal({ isOpen: false, sourceNodeId: null, position: null, defaultName: '' })}
        onSubmit={handleCreateConnectedFragment}
        sourceNodeName={fragmentModal.sourceNodeId}
        defaultName={fragmentModal.defaultName} // <-- ИЗМЕНЕНО
        generateUniqueFragmentId={generateUniqueFragmentId}
        existingIds={new Set(getNodes().map(n => n.id))}
      />
    </div>
  );
}

function Flow() {
  return (
    <ReactFlowProvider>
      <App />
    </ReactFlowProvider>
  );
}

export { getNodeColor, mediaLabel };
export default Flow;