// effectsUtils.js

/**
 * Парсит строковое значение эффекта и возвращает его в структурированном виде.
 */
export const parseEffectValue = (valueStr) => {
  valueStr = valueStr.trim();

  if (valueStr.startsWith('>') || valueStr.startsWith('<') || valueStr.startsWith('=')) return null;

  const absoluteMatch = valueStr.match(/^(-?\d+)$/);
  if (absoluteMatch) {
    const value = parseInt(absoluteMatch[1], 10);
    return { min: value, max: value, type: 'set' };
  }

  const rangeMatch = valueStr.match(/^(-?\d+)-(-?\d+)$/);
  if (rangeMatch) {
    const min = parseInt(rangeMatch[1], 10);
    const max = parseInt(rangeMatch[2], 10);
    return { min: Math.min(min, max), max: Math.max(min, max), type: 'set' };
  }

  const relativeMatch = valueStr.match(/^([+-])(\d+)$/);
  if (relativeMatch) {
    const sign = relativeMatch[1];
    const value = parseInt(relativeMatch[2], 10);
    return { min: value, max: value, type: sign === '+' ? 'add' : 'subtract' };
  }

  const relativeRangeMatch = valueStr.match(/^([+-])\((\d+)-(\d+)\)$/);
  if (relativeRangeMatch) {
    const sign = relativeRangeMatch[1];
    const val1 = parseInt(relativeRangeMatch[2], 10);
    const val2 = parseInt(relativeRangeMatch[3], 10);
    return {
      min: Math.min(val1, val2),
      max: Math.max(val1, val2),
      type: sign === '+' ? 'add' : 'subtract',
    };
  }

  return null;
};

/**
 * Применяет массив эффектов к текущему состоянию статов.
 */
export const applyEffects = (currentStats, effects) => {
  if (!effects || effects.length === 0) return;

  effects.forEach((effect) => {
    const parsed = parseEffectValue(effect.value);
    if (!parsed) return;

    const statName = effect.stat;
    if (!currentStats[statName]) {
      currentStats[statName] = { min: 0, max: 0 };
    }

    const current = currentStats[statName];

    switch (parsed.type) {
      case 'set':
        current.min = parsed.min;
        current.max = parsed.max;
        break;
      case 'add':
        current.min += parsed.min;
        current.max += parsed.max;
        break;
      case 'subtract':
        current.min -= parsed.max;
        current.max -= parsed.min;
        break;
      default:
        break;
    }
  });
};

/**
 * Основная функция для просчета эффектов по выделенным узлам.
 */
export const calculateAllEffects = (selectedNodes) => {
  if (!selectedNodes || selectedNodes.length === 0) {
    return "Не выбрано ни одного фрагмента для просчета.";
  }

  const selectedIds = new Set(selectedNodes.map((n) => n.id));
  const nodesMap = new Map(selectedNodes.map((n) => [n.id, n]));

  const targets = new Set();
  selectedNodes.forEach((node) => {
    node.data.fragment.choices?.forEach((choice) => {
      if (selectedIds.has(choice.target)) {
        targets.add(choice.target);
      }
    });
  });

  const roots = selectedNodes.filter((node) => !targets.has(node.id));

  const allPaths = [];
  const findPathsDfs = (currentNodeId, currentPath) => {
    const node = nodesMap.get(currentNodeId);
    const validChoices = node.data.fragment.choices?.filter((c) => selectedIds.has(c.target)) || [];

    if (validChoices.length === 0) {
      allPaths.push([...currentPath, currentNodeId]);
      return;
    }

    validChoices.forEach((choice) => {
      findPathsDfs(choice.target, [...currentPath, currentNodeId]);
    });
  };

  roots.forEach((rootNode) => findPathsDfs(rootNode.id, []));

  let resultText = '⚠️ Если вы добавляли эффекты, то обновите страницу для корректного подсчёта.\n\n';

  allPaths.forEach((path, index) => {
    const pathStats = {};

    for (let i = 0; i < path.length; i++) {
      const nodeId = path[i];
      const node = nodesMap.get(nodeId);

      applyEffects(pathStats, node.data.fragment.effects);

      if (i < path.length - 1) {
        const nextNodeId = path[i + 1];
        const choice = node.data.fragment.choices.find((c) => c.target === nextNodeId);
        if (choice) {
          applyEffects(pathStats, choice.effects);
        }
      }
    }

    resultText += `${index + 1}. "${path.join(' -> ')}"\nЭффекты:\n`;

    const statNames = Object.keys(pathStats);
    if (statNames.length === 0) {
      resultText += `- (нет эффектов)\n`;
    } else {
      statNames.forEach((statName) => {
        const { min, max } = pathStats[statName];
        const valueDisplay = min === max ? min : `${min} - ${max}`;
        resultText += `  - ${statName}: ${valueDisplay}\n`;
      });
    }
    resultText += `\n`;
  });

  if (allPaths.length === 0) {
    return 'Не найдено логических цепочек между выделенными фрагментами. Каждый фрагмент изолирован.';
  }

  return resultText;
};