// stripHtmlTagsPreserveNewlines.js
/**
 * Удаляет HTML-теги из строки, но сохраняет \n как есть.
 * @param {string} input
 * @returns {string}
 */
export function stripHtmlTagsPreserveNewlines(input) {
  if (!input) return '';

  // Удаляем все теги вида <tag ...>...</tag> или <tag />
  // Специально обрабатываем <a>, <span>, <blockquote expandable>, etc
  return input
    .replace(/<[^>]+>/g, '')   // удаляет все теги
    .replace(/<\/?[^>]+(>|$)/g, '') // дополнительная защита
    .replace(/&nbsp;/g, ' ')   // необязательная нормализация
    .replace(/&lt;/g, '<')     // html entity
    .replace(/&gt;/g, '>')     //
    .replace(/&amp;/g, '&');   //
}