// NoteNode.js - ИСПРАВЛЕННАЯ ВЕРСИЯ

import React, { memo, useCallback } from 'react';
import { useReactFlow } from '@xyflow/react';

const NoteNode = memo(({ data, id }) => {
  const noteStyle = {
    background: '#fffacd',
    border: '1px solid #f0e68c',
    borderRadius: '5px',
    padding: '10px',
    fontSize: '14px',
    color: '#333',
    boxShadow: '2px 2px 5px rgba(0,0,0,0.1)',
    width: 400,
    height: 'auto',
    whiteSpace: 'pre-wrap',
    wordWrap: 'break-word',
  };

  const { getNode } = useReactFlow();

  const handleContextMenu = useCallback((event) => {
    event.preventDefault(); // 💡 ВАЖНО: предотвращаем стандартное меню браузера
    const node = getNode(id);
    if (node) {
      window.dispatchEvent(new CustomEvent('noteRightClick', { detail: { event, node } }));
    }
  }, [id, getNode]);

  // 💡 ИЗМЕНЕНИЕ 1: Урезаем текст, если он длиннее 500 символов
  const rawHtml = data.text || '';
  const truncatedHtml = rawHtml.length > 1000
    ? `${rawHtml.slice(0, 1000)}...`
    : rawHtml;

  return (
    // 💡 ИЗМЕНЕНИЕ 2: Используем dangerouslySetInnerHTML для рендеринга HTML
    <div
      style={noteStyle}
      onContextMenu={handleContextMenu}
      dangerouslySetInnerHTML={{ __html: truncatedHtml }}
    />
  );
});

export default NoteNode;