// PreviewModal.js

import React, { useState, useRef, useEffect } from 'react';

// Стили для модального окна. Добавляем их прямо в компонент для простоты.
const styles = `
.preview-modal-overlay {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: rgba(0, 0, 0, 0.5);
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 1000;
}

.preview-modal-content {
    background: #282c34;
    color: white;
    padding: 20px;
    border-radius: 8px;
    width: 90%;
    max-width: 500px;
    max-height: 90vh;
    overflow-y: auto;
    border: 1px solid #444;
}

.preview-modal-content h3, .preview-modal-content h4 {
    margin-top: 0;
    border-bottom: 1px solid #555;
    padding-bottom: 8px;
    margin-bottom: 12px;
}

.preview-section {
    margin-bottom: 20px;
}

.draggable-list {
    display: flex;
    flex-direction: column;
    gap: 8px;
}

.draggable-item {
    display: flex;
    align-items: center;
    padding: 8px;
    background-color: #3a3f4a;
    border: 1px solid #555;
    border-radius: 4px;
    cursor: grab;
    user-select: none;
    justify-content: space-between; /* Новое */
}

.draggable-content {
    display: flex;
    align-items: center;
    flex-grow: 1;
    gap: 10px;
}

.sort-hint {
    font-size: 0.85em;
    color: #aaa;
    white-space: nowrap;
    margin-left: 10px;
}

.draggable-item.dragging {
    opacity: 0.5;
    border: 1px dashed #fff;
}

.drag-handle {
    margin-right: 12px;
    font-size: 1.2em;
    color: #888;
}

.media-preview-img {
    width: 40px;
    height: 40px;
    object-fit: cover;
    border-radius: 4px;
    margin-right: 10px;
}

.text-preview {
    background-color: #1e1e1e;
    padding: 10px;
    border-radius: 4px;
    white-space: pre-wrap; /* Сохраняет переносы строк */
    word-wrap: break-word;
    max-height: 200px;
    overflow-y: auto;
    border: 1px solid #555;
    line-height: 1.5; /* Улучшает читаемость */
}

/* ✨ НАЧАЛО: Добавленные стили для форматирования текста ✨ */
.text-preview a {
  color: #87CEFA; /* Light Sky Blue */
  text-decoration: none;
}
.text-preview a:hover {
  text-decoration: underline;
}

.text-preview code {
  font-family: 'Courier New', Courier, monospace;
  background-color: #3a3f4a;
  padding: 2px 5px;
  border-radius: 4px;
  font-size: 0.9em;
  border: 1px solid #555;
}

.text-preview pre {
  background-color: #1a1c20;
  border: 1px solid #555;
  padding: 10px;
  border-radius: 4px;
  white-space: pre-wrap;
  word-break: break-all;
  margin: 8px 0;
  font-family: 'Courier New', Courier, monospace;
  font-size: 0.9em;
}

.text-preview blockquote {
  margin: 8px 0 8px 10px;
  padding-left: 12px;
  border-left: 3px solid #666;
  color: #ccc;
}

.text-preview .tg-spoiler {
    background-color: #888;
    color: #888;
    border-radius: 4px;
    cursor: pointer;
    transition: all 0.15s ease-in-out;
}

.text-preview .tg-spoiler:hover {
    background-color: #999;
    color: #999;
}

.text-preview .tg-spoiler.revealed {
    background-color: transparent;
    color: inherit; /* Наследует цвет текста родителя */
}
/* ✨ КОНЕЦ: Добавленные стили для форматирования текста ✨ */

.telegram-button-preview {
    width: 100%;
    padding: 10px;
    text-align: center;
    background-color: #4a76a8;
    color: white;
    border-radius: 5px;
    font-size: 1em;
    border: none;
}

.modal-actions {
    display: flex;
    justify-content: flex-end;
    margin-top: 20px;
}
`;

function PreviewModal({ node, onClose, onUpdateMedia, onUpdateChoices }) {
    const { fragment } = node.data;

    const [media, setMedia] = useState(fragment.media || []);
    const [choices, setChoices] = useState(fragment.choices || []);

    const dragMediaRef = useRef(null);
    const dragChoiceRef = useRef(null);
    
    useEffect(() => {
        const styleSheet = document.createElement("style");
        styleSheet.innerText = styles;
        document.head.appendChild(styleSheet);
        return () => {
            document.head.removeChild(styleSheet);
        };
    }, []);

    // --- Логика Drag-and-Drop для МЕДИА ---
    const handleMediaDragStart = (e, index) => {
        dragMediaRef.current = index;
        e.dataTransfer.effectAllowed = 'move';
        e.currentTarget.classList.add('dragging');
    };

    const handleMediaDragEnter = (e, index) => {
        if (dragMediaRef.current === index) return;
        const newMedia = [...media];
        const draggedItem = newMedia.splice(dragMediaRef.current, 1)[0];
        newMedia.splice(index, 0, draggedItem);
        dragMediaRef.current = index;
        setMedia(newMedia);
    };

    const handleMediaDragEnd = (e) => {
        e.currentTarget.classList.remove('dragging');
        dragMediaRef.current = null;
        onUpdateMedia(media);
    };

    // --- Логика Drag-and-Drop для КНОПОК ---
    const handleChoiceDragStart = (e, index) => {
        dragChoiceRef.current = index;
        e.dataTransfer.effectAllowed = 'move';
        e.currentTarget.classList.add('dragging');
    };

    const handleChoiceDragEnter = (e, index) => {
        if (dragChoiceRef.current === index) return;
        const newChoices = [...choices];
        const draggedItem = newChoices.splice(dragChoiceRef.current, 1)[0];
        newChoices.splice(index, 0, draggedItem);
        dragChoiceRef.current = index;
        setChoices(newChoices);
    };

    const handleChoiceDragEnd = (e) => {
        e.currentTarget.classList.remove('dragging');
        dragChoiceRef.current = null;
        onUpdateChoices(choices);
    };

    const getMediaRepresentation = (item, index) => {
        switch (item.type) {
            case 'photo':
                return <img src={`/api/tgfile/${item.file_id}`} alt="preview" className="media-preview-img" />;
            case 'video':
                return <span>🎬 Видео_{index + 1}</span>;
            case 'animation':
                return <span>🎞️ Анимация_{index + 1}</span>;
            case 'audio':
                return <span>🎵 Аудио_{index + 1}</span>;
            default:
                return <span>❓ Неизвестный тип</span>;
        }
    };
    
    // ✨ НОВОЕ: Обработчик кликов для спойлеров
    const handleTextPreviewClick = (e) => {
        // Ищем ближайший родительский элемент-спойлер и переключаем его класс
        const spoiler = e.target.closest('.tg-spoiler');
        if (spoiler) {
            spoiler.classList.toggle('revealed');
        }
    };
    
    return (
        <div className="preview-modal-overlay" onClick={onClose}>
            <div className="preview-modal-content" onClick={(e) => e.stopPropagation()}>
                <h3>Предпросмотр: {node.id}</h3>

                <div className="preview-section">
                    <h4>Медиа</h4>
                    <div className="draggable-list">
                        {media.map((item, index) => (
                            <div
                                key={item.file_id}
                                className="draggable-item"
                                draggable
                                onDragStart={(e) => handleMediaDragStart(e, index)}
                                onDragEnter={(e) => handleMediaDragEnter(e, index)}
                                onDragEnd={handleMediaDragEnd}
                                onDragOver={(e) => e.preventDefault()}
                            >
                                <div className="draggable-content">
                                    <span className="drag-handle">⠿</span>
                                    {getMediaRepresentation(item, index)}
                                </div>
                                <span className="sort-hint">Удерживайте для сортировки</span>
                            </div>
                        ))}
                         {media.length === 0 && <p><i>Медиафайлы отсутствуют.</i></p>}
                    </div>
                </div>

                <div className="preview-section">
                    <h4>Текст</h4>
                    {/* ✨ ИЗМЕНЕНО: Добавлен onClick для спойлеров */}
                    <div
                        className="text-preview"
                        onClick={handleTextPreviewClick} 
                        dangerouslySetInnerHTML={{ __html: fragment.text?.replace(/\n/g, '<br />') || '<i>Текст отсутствует.</i>' }}
                    />
                </div>

                <div className="preview-section">
                    <h4>Кнопки</h4>
                    <div className="draggable-list">
                        {choices.map((choice, index) => (
                               <div
                                  key={`${choice.target}-${choice.text}`}
                                  className="draggable-item"
                                  draggable
                                  onDragStart={(e) => handleChoiceDragStart(e, index)}
                                  onDragEnter={(e) => handleChoiceDragEnter(e, index)}
                                  onDragEnd={handleChoiceDragEnd}
                                  onDragOver={(e) => e.preventDefault()}
                              >
                                    <div className="draggable-content">
                                        <span className="drag-handle">⠿</span>
                                        <div className="telegram-button-preview">{choice.text}</div>
                                    </div>
                              </div>
                        ))}
                        {choices.length === 0 && <p><i>Кнопки отсутствуют.</i></p>}
                    </div>
                </div>

                <div className="modal-actions">
                    <button onClick={onClose} className="modal-button primary">Готово</button>
                </div>
            </div>
        </div>
    );
}

export default PreviewModal;