import React, { useMemo } from 'react';

const MediaPreview = ({ item }) => {
    const fileUrl = useMemo(() => `/api/tgfile/${item.file_id}`, [item.file_id]);

    const previewStyle = {
        width: '60px',
        height: '60px',
        borderRadius: '4px',
        objectFit: 'cover',
        backgroundColor: '#2a2a2a',
        color: 'white',
        display: 'flex',
        alignItems: 'center',
        justifyContent: 'center',
        fontSize: '24px',
    };

    switch (item.type) {
        case 'photo':
            return <img src={fileUrl} alt="preview" style={previewStyle} />;
        case 'video':
            return <video src={fileUrl} style={previewStyle} playsInline muted loop />;
        case 'animation':
            return <video src={fileUrl} style={previewStyle} autoPlay playsInline muted loop />;
        case 'audio':
            return <div style={previewStyle}>🎵</div>;
        default:
            return <div style={previewStyle}>❓</div>;
    }
};

export default MediaPreview;