import React from 'react';
import { useSortable } from '@dnd-kit/sortable';
import { CSS } from '@dnd-kit/utilities';
import MediaPreview from './MediaPreview';

export function SortableMediaItem({ item, onDelete }) {
    const {
        attributes,
        listeners,
        setNodeRef,
        transform,
        transition,
    } = useSortable({ id: item.file_id });

    const style = {
        transform: CSS.Transform.toString(transform),
        transition,
        display: 'flex',
        alignItems: 'center',
        justifyContent: 'space-between',
        background: '#3a3a3a',
        padding: '10px',
        borderRadius: '8px',
        gap: '15px',
        touchAction: 'none',
    };

    return (
        <li ref={setNodeRef} style={style} {...attributes}>
            {/* Иконка для захвата */}
            <span
                {...listeners}
                style={{
                    cursor: 'grab',
                    color: 'white',
                    padding: '0 5px',
                    userSelect: 'none',
                }}
                title="Перетащите, чтобы изменить порядок"
            >
                &#x2630;
            </span>

            {/* Миниатюра тоже становится зоной перетаскивания */}
            <div {...listeners} style={{ cursor: 'grab' }}>
                <MediaPreview item={item} />
            </div>

            <div
                style={{
                    flex: 1,
                    overflow: 'hidden',
                    display: 'flex',
                    flexDirection: 'column',
                    gap: '5px',
                }}
            >
                <span style={{ textTransform: 'capitalize', fontWeight: 'bold' }}>{item.type}</span>
                <span
                    style={{
                        fontFamily: 'monospace',
                        fontSize: '0.8rem',
                        color: '#aaa',
                        textOverflow: 'ellipsis',
                        whiteSpace: 'nowrap',
                        overflow: 'hidden',
                    }}
                >
                    {item.file_id}
                </span>
            </div>

            <button
                className="modal-button danger"
                style={{ padding: '5px 10px' }}
                onClick={onDelete}
            >
                🗑️
            </button>
        </li>
    );
}