// EffectRow.js

import React, { useState } from 'react';
import ProbabilityModal from './ProbabilityModal'; // Модальное окно для вероятностей

// Стили для удобства
const rowStyle = {
    display: 'flex', // Изменено с grid на flex
    flexDirection: 'column', // Элементы теперь в колонку (две строки)
    gap: '10px', // Пространство между строками
    border: '1px solid #ccc',
    padding: '10px',
    borderRadius: '8px',
    background: '#f9f9f9',
};
const inputStyle = { padding: '8px', borderRadius: '4px', border: '1px solid #ddd', width: '100%', boxSizing: 'border-box' };
const selectStyle = { ...inputStyle };
const buttonStyle = { padding: '8px 12px', borderRadius: '4px', border: 'none', cursor: 'pointer' };
const smallButtonStyle = {
    ...buttonStyle,
    padding: '4px 8px',
    fontSize: '12px',
    maxWidth: '100px', // ограничиваем ширину
    whiteSpace: 'normal', // разрешаем перенос строк
    textAlign: 'center', // выравнивание по центру
    lineHeight: '1.2', // более плотный текст
};

function EffectRow({ effect, onChange, onDelete }) {
    const [isProbModalOpen, setIsProbModalOpen] = useState(false);

    const handleChange = (field, value) => {
        onChange({ ...effect, [field]: value });
    };

    const renderValueInput = () => {
        switch (effect.type) {
            case 'set':
                return <input type="number" style={inputStyle} placeholder="Значение" value={effect.value || ''} onChange={(e) => handleChange('value', e.target.value)} />;
            case 'modify':
                return (
                    <div style={{ display: 'flex', gap: '5px' }}>
                        <select style={selectStyle} value={effect.modifyType || 'increase'} onChange={(e) => handleChange('modifyType', e.target.value)}>
                            <option value="increase">Увеличить</option>
                            <option value="decrease">Уменьшить</option>
                        </select>
                        <input type="number" style={inputStyle} placeholder="На..." value={effect.value || ''} onChange={(e) => handleChange('value', e.target.value)} />
                    </div>
                );
            case 'check':
                return (
                    <div style={{ display: 'flex', gap: '5px' }}>
                        <select style={selectStyle} value={effect.checkType || 'equal'} onChange={(e) => handleChange('checkType', e.target.value)}>
                            <option value="greater">{'>'} (Больше)</option>
                            <option value="less">{'<'} (Меньше)</option>
                            <option value="equal">{'='} (Равно)</option>
                        </select>
                        <input type="number" style={inputStyle} placeholder="Значение" value={effect.value || ''} onChange={(e) => handleChange('value', e.target.value)} />
                    </div>
                );
            case 'range':
            case 'modifyRange':
                return (
                    <div style={{ display: 'flex', alignItems: 'center', gap: '8px' }}>
                        <input type="number" style={inputStyle} placeholder="От" value={effect.from || ''} onChange={(e) => handleChange('from', e.target.value)} />
                        <span>-</span>
                        <input type="number" style={inputStyle} placeholder="До" value={effect.to || ''} onChange={(e) => handleChange('to', e.target.value)} />
                        <button style={{ ...smallButtonStyle, background: '#e0e0e0' }} onClick={() => setIsProbModalOpen(true)}>
                            🎲 Настроить вероятности
                        </button>
                    </div>
                );
            default:
                return null;
        }
    };

    return (
        <div style={rowStyle}>
            {/* --- Первая строка: Название атрибута и Тип --- */}
            <div style={{ display: 'flex', gap: '10px' }}>
                <div style={{ flex: 2 }}>
                    <input
                        type="text"
                        placeholder="Атрибут (сила, удача...)"
                        value={effect.stat}
                        onChange={(e) => handleChange('stat', e.target.value)}
                        style={inputStyle}
                    />
                </div>
                <div style={{ flex: 1 }}>
                    <select style={selectStyle} value={effect.type} onChange={(e) => handleChange('type', e.target.value)}>
                        <option value="set">Задать</option>
                        <option value="modify">Изменить</option>
                        <option value="check">Проверка</option>
                        <option value="range">Диапазон</option>
                        <option value="modifyRange">Изменить на диапазон</option>
                    </select>
                </div>
                {/* Селект Увеличить/Уменьшить — ПЕРЕМЕЩЁН сюда */}
                {effect.type === 'modifyRange' && (
                    <div style={{ flex: 1 }}>
                        <select
                            style={selectStyle}
                            value={effect.modifyType || 'increase'}
                            onChange={(e) => handleChange('modifyType', e.target.value)}
                        >
                            <option value="increase">Увеличить</option>
                            <option value="decrease">Уменьшить</option>
                        </select>
                    </div>
                )}
            </div>

            {/* --- Вторая строка: Логика и Действия --- */}
            <div style={{ display: 'flex', gap: '10px', alignItems: 'center' }}>
                {/* Контейнер для динамических полей ввода (занимает всё доступное место) */}
                <div style={{ flex: 1 }}>
                    {renderValueInput()}
                </div>
                
                {/* Опции и удаление */}
                <div style={{ display: 'flex', alignItems: 'center', gap: '10px' }}>
                    <label title="Скрыть эффект от игрока" style={{ display: 'flex', alignItems: 'center', gap: '5px', cursor: 'pointer' }}>
                        <input type="checkbox" checked={!!effect.hide} onChange={(e) => handleChange('hide', e.target.checked)} />
                        Скрыть
                    </label>
                    <button style={{ ...buttonStyle, background: '#f8d7da', color: '#721c24' }} onClick={onDelete}>
                        🗑️
                    </button>
                </div>
            </div>
            
            {/* Модальное окно вероятностей (остаётся без изменений) */}
            {isProbModalOpen && (
                <ProbabilityModal
                    isOpen={isProbModalOpen}
                    onClose={() => setIsProbModalOpen(false)}
                    from={effect.from}
                    to={effect.to}
                    modifiers={effect.modifiers || []}
                    onSave={(newModifiers) => {
                        handleChange('modifiers', newModifiers);
                        setIsProbModalOpen(false);
                    }}
                />
            )}
        </div>
    );
}

export default EffectRow;