// CreateFragmentModal.js
// CreateFragmentModal.js
import React, { useState, useEffect } from 'react';
import './ModalStyles.css'; // Импортируем стили

const HelpModal = ({ isOpen, onClose }) => {
  if (!isOpen) return null;

  return (
    <div className="modal-overlay"> {/* Добавляем оверлей */}
      <div className="modal-content help-modal-content">
        <h3>Правила именования фрагмента</h3>
        <p>
          1) Имя фрагмента должно содержать латиницу, кириллицу, одно нижнее подчёркивание и число. Например: "ИдтиПрямо_2", "Встать_8"{'\n'}
          2) Максимум 17 символов{'\n'}
          3) Вы можете не вводить число — оно добавится автоматически{'\n'}
          4) Вы можете вообще не указывать имя — будет использовано имя предыдущего фрагмента с ближайшим незанятым номером
        </p>
        <div style={{ textAlign: 'right', marginTop: '15px' }}>
          <button onClick={onClose} className="button-base ok-button">Понятно</button>
        </div>
      </div>
    </div>
  );
};

export const CreateFragmentModal = ({ isOpen, onClose, onSubmit, defaultName, sourceNodeName, generateUniqueFragmentId, existingIds }) => {
  const [fragmentName, setFragmentName] = useState('');
  const [choiceText, setChoiceText] = useState('');
  const [error, setError] = useState('');
  const [isSubmitting, setIsSubmitting] = useState(false);
  const [isHelpOpen, setIsHelpOpen] = useState(false);

  useEffect(() => {
    if (isOpen) {
      setFragmentName('');
      setChoiceText('');
      setError('');
      setIsSubmitting(false);
    }
  }, [isOpen]);

  const handleSubmit = async () => {
    if (isSubmitting) return;

    let baseName = fragmentName.trim();
    if (!baseName) {
        baseName = defaultName?.trim();
    }

    if (!baseName) {
        setError("Имя фрагмента не может быть пустым.");
        return;
    }

    let finalFragmentName;

    const nameHasSuffix = /_\d+$/.test(baseName);

    if (nameHasSuffix) {
        finalFragmentName = baseName;
    } else {
        const generationResult = generateUniqueFragmentId(baseName, existingIds, false);
        finalFragmentName = generationResult.id;
    }

    const validation = generateUniqueFragmentId(finalFragmentName, existingIds, true);
    if (validation.error) {
        setError(validation.error);
        return;
    }

    if (!choiceText.trim()) {
        setError("Текст кнопки не может быть пустым.");
        return;
    }
    
    setIsSubmitting(true);
    setError('');

    try {
        await onSubmit(finalFragmentName, choiceText.trim());
    } catch (e) {
        setError(e.message || 'Произошла неизвестная ошибка');
    } finally {
        setIsSubmitting(false);
    }
  };

  if (!isOpen) {
    return null;
  }

  return (
    <div className="modal-overlay"> {/* Добавляем оверлей */}
      <div className="modal-content">
        <h3>Новый фрагмент от "{sourceNodeName}"</h3>
        
        <div>
          <label htmlFor="fragmentName" style={{ display: 'block', marginBottom: '5px' }}>Имя нового фрагмента:</label>
          <input
            id="fragmentName"
            type="text"
            value={fragmentName}
            onChange={(e) => {
              setFragmentName(e.target.value);
              if (error) setError('');
            }}
            placeholder={defaultName || "Например: Сцена_1"}
            className="input-field"
          />
          <div className="error-text">{error}</div>
        </div>

        <div>
          <label htmlFor="choiceText" style={{ display: 'block', marginBottom: '5px' }}>Текст кнопки для перехода:</label>
          <input
            id="choiceText"
            type="text"
            value={choiceText}
            onChange={(e) => setChoiceText(e.target.value)}
            placeholder="Например: Идти дальше"
            className="input-field"
          />
        </div>
        
        <div style={{ display: 'flex', justifyContent: 'flex-end', gap: '10px', marginTop: '15px' }}>
          <button onClick={onClose} className="button-base cancel-button" disabled={isSubmitting}>Отмена</button>
          <button onClick={handleSubmit} className="button-base ok-button" disabled={isSubmitting}>
              {isSubmitting ? 'Создание...' : 'ОК'}
          </button>
        </div>

        <button
          onClick={() => setIsHelpOpen(true)}
          className="help-button"
        >
          Помощь
        </button>

        <HelpModal isOpen={isHelpOpen} onClose={() => setIsHelpOpen(false)} />
      </div>
    </div>
  );
};