// MediaManagerModal.js

import React, { useState, useCallback, useMemo } from 'react';
import { DndContext, closestCenter } from '@dnd-kit/core';
import { arrayMove, SortableContext, verticalListSortingStrategy } from '@dnd-kit/sortable';
import UploadMediaButton from './UploadMediaButton';
import { apiUpdateFragmentMedia } from './api';
import { SortableMediaItem } from './SortableMediaItem'; 

export default function MediaManagerModal({ node, onUpdate, onClose, userId, storyId }) {
    const [mediaItems, setMediaItems] = useState(node.data.fragment.media || []);
    const [isUploading, setIsUploading] = useState(false);
    const [isDragOver, setIsDragOver] = useState(false);

    const handleSaveOrder = useCallback(async (newOrderedMedia) => {
        try {
            await apiUpdateFragmentMedia(userId, storyId, node.id, newOrderedMedia);
            onUpdate(newOrderedMedia);
        } catch (error) {
            console.error("Failed to save media order:", error);
            alert(`Не удалось сохранить порядок медиа: ${error.message}`);
            setMediaItems(mediaItems);
        }
    }, [userId, storyId, node.id, onUpdate, mediaItems]);

    const handleDragEnd = (event) => {
        const { active, over } = event;
        if (active.id !== over.id) {
            setMediaItems((items) => {
                const oldIndex = items.findIndex(item => item.file_id === active.id);
                const newIndex = items.findIndex(item => item.file_id === over.id);
                const newOrderedItems = arrayMove(items, oldIndex, newIndex);
                handleSaveOrder(newOrderedItems);
                return newOrderedItems;
            });
        }
    };

    const handleUploadSuccess = useCallback(async (newMediaObject) => {
        if (mediaItems.length >= 10) {
            alert("Нельзя загрузить больше 10 медиа.");
            return;
        }

        const updatedMedia = [...mediaItems, newMediaObject];
        try {
            await apiUpdateFragmentMedia(userId, storyId, node.id, updatedMedia);
            setMediaItems(updatedMedia);
            onUpdate(updatedMedia);
        } catch (error) {
            console.error("Failed to add media:", error);
            alert(`Не удалось добавить медиа: ${error.message}`);
        }
    }, [mediaItems, userId, storyId, node.id, onUpdate]);

    const handleDeleteMedia = useCallback(async (indexToDelete) => {
        const updatedMedia = mediaItems.filter((_, index) => index !== indexToDelete);
        try {
            await apiUpdateFragmentMedia(userId, storyId, node.id, updatedMedia);
            setMediaItems(updatedMedia);
            onUpdate(updatedMedia);
        } catch (error) {
            console.error("Failed to delete media:", error);
            alert(`Не удалось удалить медиа: ${error.message}`);
        }
    }, [mediaItems, userId, storyId, node.id, onUpdate]);

    const handleDrop = async (e) => {
        e.preventDefault();
        setIsDragOver(false);

        const files = Array.from(e.dataTransfer.files);
        const filesToUpload = files.slice(0, 10 - mediaItems.length);
        for (const file of filesToUpload) {
            if (mediaItems.length >= 10) {
                alert("Нельзя загрузить больше 10 медиа.");
                return;
            }            
            const formData = new FormData();
            formData.append('file', file);
            formData.append('user_id', userId);
            setIsUploading(true);

            try {
                const res = await fetch('/api/upload_media', {
                    method: 'POST',
                    body: formData
                });
                const result = await res.json();
                if (res.ok && result.file_id) {
                    await handleUploadSuccess(result);
                } else {
                    alert(result.error || 'Ошибка загрузки на сервер Telegram');
                }
            } catch (err) {
                console.error('Ошибка при загрузке файла:', err);
                alert('Критическая ошибка при загрузке файла. См. консоль.');
            } finally {
                setIsUploading(false);
            }
        }
    };

    const sortableItems = mediaItems.map(item => item.file_id);

    return (
        <div className="modal-overlay" onClick={onClose}>
            <div 
                className="modal-content"
                onClick={(e) => e.stopPropagation()}
                onDragOver={(e) => { e.preventDefault(); setIsDragOver(true); }}
                onDragLeave={() => setIsDragOver(false)}
                onDrop={handleDrop}
                style={{ gap: '20px', border: isDragOver ? '2px dashed #888' : 'none' }}
            >
                <h3>Медиа для фрагмента "{node.id}"</h3>

                <div style={{ maxHeight: '60vh', overflowY: 'auto', paddingRight: '10px' }}>
                    {mediaItems.length === 0 ? (
                        <p>Медиафайлы отсутствуют. Воспользуйтесь кнопкой или перетащите их в это окно.</p>
                    ) : (
                        <DndContext
                            collisionDetection={closestCenter}
                            onDragEnd={handleDragEnd}
                        >
                            <SortableContext
                                items={sortableItems}
                                strategy={verticalListSortingStrategy}
                            >
                                <ul style={{ listStyle: 'none', padding: 0, margin: 0, display: 'flex', flexDirection: 'column', gap: '10px' }}>
                                    {mediaItems.map((item, index) => (
                                        <SortableMediaItem 
                                            key={item.file_id}
                                            item={item}
                                            onDelete={() => handleDeleteMedia(index)}
                                        />
                                    ))}
                                </ul>
                            </SortableContext>
                        </DndContext>
                    )}
                </div>

                <div className="modal-actions" style={{ justifyContent: 'space-between' }}>
                    <button className="modal-button secondary" onClick={onClose}>Закрыть</button>
                    {isUploading ? (
                        <p>Загрузка...</p>
                    ) : (
                        <UploadMediaButton
                            userId={userId}
                            onUpload={handleUploadSuccess}
                            onUploadStart={() => setIsUploading(true)}
                            onUploadEnd={() => setIsUploading(false)}
                        />
                    )}
                </div>
            </div>
        </div>
    );
}