// CustomEdge.js
import React from 'react';
import { getBezierPath, EdgeLabelRenderer } from '@xyflow/react';

const CustomEdge = ({
  id,
  sourceX,
  sourceY,
  targetX,
  targetY,
  sourcePosition,
  targetPosition,
  style = {},
  data, // Здесь хранятся наши данные: text и effectsText
}) => {
  // Эта функция из библиотеки @xyflow/react вычисляет SVG-путь для кривой Безье
  // и координаты для размещения подписи по центру
  const [edgePath, labelX, labelY] = getBezierPath({
    sourceX,
    sourceY,
    sourcePosition,
    targetX,
    targetY,
    targetPosition,
  });

  // Уникальный ID для маркера-стрелки, чтобы стили не пересекались
  const markerEndId = `arrow-${id}`;
  
  // Interaction (onEdgeClick) была убрана, т.к. теперь используется контекстное меню.
  // Это делает интерфейс чище и предсказуемее.

  return (
    <>
      {/* Мы рисуем SVG-элементы (линию и стрелку) отдельно,
        а подпись рендерим как обычный HTML-элемент (div) с помощью EdgeLabelRenderer.
        Это дает больше гибкости в стилизации подписи.
      */}

      {/* Контейнер для определения маркера-стрелки. Он невидимый. */}
      <svg style={{ height: 0, width: 0, position: 'absolute' }}>
        <defs>
          <marker
            id={markerEndId}
            viewBox="0 0 10 10"
            refX="10"
            refY="5"
            markerWidth="6"
            markerHeight="6"
            orient="auto-start-reverse"
          >
            {/* Это форма самой стрелки - треугольник */}
            <path d="M 0 0 L 10 5 L 0 10 z" fill="#2563eb" />
          </marker>
        </defs>
      </svg>

      {/* Сама линия ребра */}
      <path
        id={id}
        style={style} // Сюда могут приходить стили из основного компонента
        className="react-flow__edge-path" // Стандартный класс для стилизации
        d={edgePath} // SVG-путь, вычисленный ранее
        markerEnd={`url(#${markerEndId})`} // Указываем, что на конце должна быть наша стрелка
      />

      {/* Этот компонент позволяет разместить HTML-элемент (нашу подпись) точно над ребром */}
      <EdgeLabelRenderer>
        <div
          style={{
            position: 'absolute',
            // transform позиционирует центр div'а в точке (labelX, labelY)
            transform: `translate(-50%, -50%) translate(${labelX}px,${labelY}px)`,
            // Меняем фон, если у связи есть эффекты, для визуального акцента
            background: data?.effectsText ? '#dbeafe' : '#f1f5f9',
            padding: '4px 8px',
            borderRadius: '5px',
            fontSize: '12px',
            textAlign: 'center',
            color: '#000',
            // Контекстное меню будет открываться по правому клику на ребре,
            // поэтому клик по подписи не нужен.
            pointerEvents: 'all' 
          }}
          // Эти классы говорят React Flow не начинать перетаскивание холста или узла при клике на подпись
          className="nodrag nopan" 
        >
          {/* Отображаем текст выбора жирным */}
          <strong>{data?.text}</strong>
          
          {/* Отображаем блок с эффектами, только если они есть.
            Используем data.effectsText, так как это уже готовая отформатированная строка.
          */}
          {data?.effectsText && (
            <div style={{ fontSize: '10px', color: '#334155', marginTop: '3px' }}>
              ⚙️ {data.effectsText}
            </div>
          )}
        </div>
      </EdgeLabelRenderer>
    </>
  );
};

export default CustomEdge;