// NoteContextMenu.js
import React, { useState, useEffect, useCallback } from 'react';
import { TextEditorModal, ActionModal } from './ContextMenu';
import { apiUpdateNote, apiDeleteNote } from './api'; // ПРЕДПОЛАГАЕТСЯ, ЧТО ВЫ ДОБАВИТЕ ЭТИ ФУНКЦИИ

// Получаем ID пользователя и истории один раз
const pathname = window.location.pathname;
const [userId, storyId] = pathname.slice(1).split('_');

function NoteContextMenu({ node, setNodes, onClose, top, left, autoOpenEditor = false }) {
  const [modal, setModal] = useState(null);

  // Автоматическое открытие редактора при создании
  useEffect(() => {
    if (autoOpenEditor) {
      setModal('edit');
    }
  }, [autoOpenEditor]);
  useEffect(() => {
    console.log('[DEBUG] NoteContextMenu открыт для:', node?.id);
  }, [node]);


    // Обработчик сохранения текста из редактора
    const handleSaveText = useCallback(async (newText) => {
        try {
            // 1. Вызываем API для обновления заметки
            await apiUpdateNote(userId, storyId, node.id, newText);

            // 2. Обновляем узел локально для мгновенного отклика
            setNodes((nds) =>
                nds.map((n) => {
                    if (n.id === node.id) {
                        return { ...n, data: { ...n.data, text: newText } };
                    }
                    return n;
                })
            );
        } catch (e) {
            alert(`Ошибка обновления заметки: ${e.message}`);
        } finally {
            setModal(null); // Закрываем редактор
            onClose(); // Закрываем контекстное меню
        }
    }, [node.id, setNodes, onClose]);

    // Обработчик удаления заметки
    const handleDelete = useCallback(async () => {
        try {
            // 1. Вызываем API для удаления
            await apiDeleteNote(userId, storyId, node.id);
            // 2. Удаляем узел с канваса
            setNodes((nds) => nds.filter((n) => n.id !== node.id));
        } catch (e) {
            alert(`Ошибка удаления заметки: ${e.message}`);
        } finally {
            setModal(null);
            onClose();
        }
    }, [node.id, setNodes, onClose]);

    // Определяем позицию меню
      const style = {
        top: `${top}px`,
        left: `${left}px`,
        position: 'fixed', // Можно заменить на 'fixed' для надежности
        zIndex: 1000
      };

      return (
        <>
          {!autoOpenEditor && ( // Скрываем меню при авто-открытии
            <div style={style} className="context-menu">
              <p>Заметка: <strong>{node.id}</strong></p>
              <button onClick={() => setModal('edit')}>📝 Редактировать</button>
              <button className="delete" onClick={() => setModal('delete')}>🗑️ Удалить</button>
              <button onClick={onClose}>❌ Закрыть</button>
            </div>
          )}

          {modal === 'edit' && (
            <TextEditorModal
              initialHtml={node.data.text || ''}
              onSave={handleSaveText}
              onCancel={() => { setModal(null); onClose(); }}
            />
          )}

            {modal === 'delete' && (
                <ActionModal
                    title="Подтвердите удаление"
                    type="danger"
                    onConfirm={handleDelete}
                    onCancel={() => { setModal(null); onClose(); }}
                    confirmText="Да, удалить"
                >
                    <p>Вы уверены, что хотите удалить заметку?</p>
                </ActionModal>
            )}
        </>
    );
}

export default NoteContextMenu;